import {
  Component,
  OnInit,
  Inject,
  Injectable,
  ViewChild,
} from '@angular/core';
import {
  LienHeClientModel,
  LienHeClientModelReq,
} from '../../../../../shared/models/lien-he-client.model';
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
} from '@angular/material/dialog';
import { XemThemLienhHeClientService } from './xem-them-lien-he-client.service';
//import { ProgressDialogComponent } from '../../../../../shared/pages/progress-dialog/progress-dialog.component';
import { AlertDialogComponent } from '../../../../../shared/pages/alert-dialog/alert-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { LienHeClientComponent } from '../lien-he-client.component';
import { environment } from 'src/environments/environment';
import { Router } from '@angular/router';

@Component({
  selector: 'app-xem-them-lien-he-client',
  templateUrl: './xem-them-lien-he-client.component.html',
  styleUrls: ['./xem-them-lien-he-client.component.scss'],
})
export class XemThemLienHeClientComponent implements OnInit {
  showProgressBar = false;

  _lienHeClient = new LienHeClientModelReq(new LienHeClientModel(null));
  _noiDungPhanHoi: LienHeClientModel[];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public progressDialog: MatDialog,
    private __service: XemThemLienhHeClientService,
    private __snackbar: MatSnackBar,
    private __router: Router,
    public dialogMainRef: MatDialogRef<LienHeClientComponent>
  ) {}

  _Name = this.data.lienHeClient.Name;
  _Email = this.data.lienHeClient.Email;
  _NguoiCapNhat = this.data.lienHeClient.NguoiCapNhat;
  _NgayCapNhat = this.data.lienHeClient.NgayCapNhat;
  _Message = this.data.lienHeClient.Message;
  _id = this.data.lienHeClient.id;
  _idGoc = this.data.lienHeClient.idGoc;

  private getDataLienHeClient() {
    this.showProgressBar = true;

    this.__service.GetIdGocByClient(this._id).subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
                this._noiDungPhanHoi = data;
        this._noiDungPhanHoi.push(this.data.lienHeClient);
        this._noiDungPhanHoi.sort((s1, s2) => {
          if (s1.NgayCapNhat >= s2.NgayCapNhat) return 1;
          else return -1;
        });
      },

      (error) => {
        this.dialogMainRef.close({
          result: false,
        });
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  showLogin() {
    this.__router.navigate(['login']);
  }
  ngOnInit(): void {
    this.getDataLienHeClient();
    this._lienHeClient.Name = '';
    this._lienHeClient.Email = '';
    this._lienHeClient.Message = '';
  }

  add() {
    this._lienHeClient.idGoc = this._id;
    this._lienHeClient.Name = this._Name;
    this._lienHeClient.Email = this._Email;

    if (
      this._lienHeClient.Message == null ||
      this._lienHeClient.Message == ''
    ) {
      this.__snackbar.open('Vui lòng nhập nội dung phản hồi', null, {
        duration: 2000,
      });
    } else {
      this.showProgressBar = true;

      this.__service.addByClient(this._lienHeClient).subscribe(
        (data) => {
          this.showProgressBar = false;
          if (data === 'true') {
            this.__snackbar.open('Gửi câu trả lời thành công', null, {
              duration: 2000,
            });
            this.getDataLienHeClient();
            this._lienHeClient.Message = '';
          } else {
            this.__snackbar.open(data, null, {
              duration: 2000,
            });
          }
        },

        (error) => {
          this.dialogMainRef.close({
            result: false,
          });
          this.showLogin();
          this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
            duration: 2000,
          });
        }
      );
    }
  }
}
