import { inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../../environments/environment';
import { LienHeClientModel, LienHeClientModelReq } from '../../../../../shared/models/lien-he-client.model';
import { env } from 'process';
import { id } from '@swimlane/ngx-charts';

@Injectable({ providedIn: 'root' })
export class AddLienHeClientService {
    constructor( private httpClient: HttpClient ) {}

    add(lienHeClient: LienHeClientModelReq): Observable<any> {
        let url = environment.api.url + 'LienHeQuaEmail/add';
        return this.httpClient.post(url, lienHeClient);
    }
         
    getInfo(): Observable<any> {
        let url = environment.api.url + 'User/getInfo';
        return this.httpClient.get(url);
    }
}