import { inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../../environments/environment';
import { LienHeClientModel, LienHeClientModelReq } from '../../../../../shared/models/lien-he-client.model';
import { env } from 'process';
import { id } from '@swimlane/ngx-charts';

@Injectable({ providedIn: 'root' })
export class XemThemLienhHeClientService {
    constructor( private httpClient: HttpClient ) {}

    GetbyClient(): Observable<any> {
        let url = environment.api.url + 'LienHeQuaEmail/getAll';
        return this.httpClient.get(url);
    }

    GetIdGocByClient(idGoc: string): Observable<any> {
        let url = environment.api.url + 'LienHeQuaEmail/getByIDGoc?id=' + idGoc;
        return this.httpClient.get(url);
    }

    addByClient(lienHeClient: LienHeClientModelReq): Observable<any> {
        let url = environment.api.url + 'LienHeQuaEmail/add';
        return this.httpClient.post(url, lienHeClient);
    }

}