import {
  Component,
  OnInit,
  Inject,
  Injectable,
  ViewChild,
} from '@angular/core';
import {
  LienHeClientModel,
  LienHeClientModelReq,
} from '../../../../../shared/models/lien-he-client.model';
import {
  InfoUserModel,
  InfoUserModelReq,
} from '../../../../../shared/models/info-user.model';
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
} from '@angular/material/dialog';
import { AddLienHeClientService } from './add-lien-he-client.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { LienHeClientComponent } from '../lien-he-client.component';
import { UserModel } from 'src/app/shared/models/user.model';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-add-lien-he-client',
  templateUrl: './add-lien-he-client.component.html',
  styleUrls: ['./add-lien-he-client.component.scss'],
})
export class AddLienHeClientComponent implements OnInit {
  _lienHeClient = new LienHeClientModelReq(new LienHeClientModel(null));
  _infoUser = new InfoUserModelReq(new InfoUserModel(null));

  showProgressBar = false;
  constructor(
    private __service: AddLienHeClientService,
    private __snackbar: MatSnackBar,
    private __router: Router,
    public dialogMainRef: MatDialogRef<LienHeClientComponent>
  ) {}

  private getDataInfoUser() {
    this.showProgressBar = true;

    this.__service.getInfo().subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data.Message) {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return;
        }
        this._infoUser = data;
      },

      (error) => {
        this.dialogMainRef.close({
          result: false,
        });
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  ngOnInit(): void {
    this.getDataInfoUser();
    this._lienHeClient.Message = '';
  }

  add() {
    if (
      this._lienHeClient.Message == '' ||
      this._lienHeClient.Message == null
    ) {
      this.__snackbar.open('Phải nhập đầy đủ thông tin liên hệ', null, {
        duration: 2000,
      });
    } else {
      this._lienHeClient.Name = this._infoUser.DisplayName;
      this._lienHeClient.Email = this._infoUser.Email;

      this.showProgressBar = true;

      this.__service.add(this._lienHeClient).subscribe(
        (data) => {
          this.showProgressBar = false;
          if (data === 'true') {
            this.__snackbar.open('Gửi liên hệ thành công', null, {
              duration: 2000,
            });
            this.dialogMainRef.close({
              data: this._lienHeClient,
              result: true,
            });
            location.reload();
          } else {
            this.__snackbar.open(data, null, {
              duration: 2000,
            });
          }
        },

        (error) => {
          this.dialogMainRef.close({
            result: false,
          });
          this.showLogin();
          this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
            duration: 2000,
          });
        }
      );
    }
  }
  showLogin() {
    this.__router.navigate(['login']);
  }
}
