export class LienHeClientModel {
    id: string
    Name: string
    Email: string
    Message: string
    idGoc: string
    NguoiCapNhat: string
    NgayCapNhat: Date
    isClient: boolean
    STT: number

    constructor(lienHeClientModelReq: LienHeClientModelReq | null) {
        if (lienHeClientModelReq != null && lienHeClientModelReq instanceof LienHeClientModelReq) {
            this.id = lienHeClientModelReq.id
            this.Name = lienHeClientModelReq.Name
            this.Email = lienHeClientModelReq.Email
            this.Message = lienHeClientModelReq.Message
            this.idGoc = lienHeClientModelReq.idGoc
            this.NguoiCapNhat = lienHeClientModelReq.NguoiCapNhat
            this.NgayCapNhat = lienHeClientModelReq.NgayCapNhat
            this.STT = -1
        }

        else {
            this.id = ""
            this.Name = ""
            this.Email = ""
            this.Message = ""
            this.idGoc = ""
            this.NguoiCapNhat = ""
            this.NgayCapNhat = null
            this.STT = -1
        }
    }
}

export class LienHeClientModelReq {
    id: string
    Name: string
    Email: string
    Message: string
    idGoc: string
    NguoiCapNhat: string
    NgayCapNhat: Date
    HienThi: Boolean
    STT: number

    constructor(lienHeClient: LienHeClientModel) {
            this.id = 'lienHeClient.id'
            this.Name = "lienHeClient.Name"
            this.Email = "lienHeClient.Email"
            this.Message = 'lienHeClient.Message'
            this.idGoc = 'lienHeClient.idGoc'
            this.NguoiCapNhat = 'lienHeClient.NguoiCapNhat'
            this.HienThi = true
            this.STT = -1
    }
}