export class InfoUserModel {
    UserId: string
    Username: string
    DisplayName: string
    Status: string
    LockDate: string
    RoleId: string
    RoleName: string
    Email: string
    Avatar: string
    Birthday: string
    Password: string
    PasswordConfirm: string
    PhoneNumber: string

    constructor(infoUserModelReq: InfoUserModelReq | null) {
        if (infoUserModelReq != null && infoUserModelReq instanceof InfoUserModelReq) {
            this.UserId = infoUserModelReq.UserId
            this.Username = infoUserModelReq.Username
            this.DisplayName = infoUserModelReq.DisplayName
            this.Status = infoUserModelReq.Status
            this.LockDate = infoUserModelReq.LockDate
            this.RoleId = infoUserModelReq.RoleId
            this.RoleName = infoUserModelReq.RoleName
            this.Email = infoUserModelReq.Email
            this.Avatar = infoUserModelReq.Avatar
            this.Birthday = infoUserModelReq.Birthday
            this.Password = infoUserModelReq.Password
            this.PasswordConfirm = infoUserModelReq.PasswordConfirm
            this.PhoneNumber = infoUserModelReq.PhoneNumber
        }

        else {
            this.UserId = ""
            this.Username = ""
            this.DisplayName = ""
            this.Status = ""
            this.LockDate = ""
            this.RoleId = ""
            this.RoleName = ""
            this.Email = ""
            this.Avatar = ""
            this.Birthday = ""
            this.Password = ""
            this.PasswordConfirm = ""
            this.PhoneNumber = ""
        }
    }
}

export class InfoUserModelReq {
    UserId: string
    Username: string
    DisplayName: string
    Status: string
    LockDate: string
    RoleId: string
    RoleName: string
    Email: string
    Avatar: string
    Birthday: string
    Password: string
    PasswordConfirm: string
    PhoneNumber: string

    constructor(infoUserModel: InfoUserModel) {
        this.UserId = 'infoUserModel.UserId'
        this.Username = 'infoUserModel.Username'
        this.DisplayName = 'infoUserModel.DisplayName'
        this.Status = 'infoUserModel.Status'
        this.LockDate = 'infoUserModel.LockDate'
        this.RoleId = 'infoUserModel.RoleId'
        this.RoleName = 'infoUserModel.RoleName'
        this.Email = 'infoUserModel.Email'
        this.Avatar = 'infoUserModel.Avatar'
        this.Birthday = 'infoUserModel.Birthday'
        this.Password = 'infoUserModel.Password'
        this.PasswordConfirm = 'infoUserModel.PasswordConfirm'
        this.PhoneNumber = 'infoUserModel.PhoneNumber'
    }
}