import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { LienHeEmailModel } from 'src/app/shared/models/lien-he-qua-email.model';
import { MatTableDataSource } from '@angular/material/table';
import { LienHeClientModule } from './lien-he-client.module';
import { AddLienHeClientComponent } from './add-lien-he-client/add-lien-he-client.component';
import {
  LienHeClientModel,
  LienHeClientModelReq,
} from 'src/app/shared/models/lien-he-client.model';
import { XemThemLienHeClientComponent } from './xem-them-lien-he-client/xem-them-lien-he-client.component';
import { environment, eMenuClient } from 'src/environments/environment';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { LienHeClientService } from './lien-he-client.service';
import { MainNavComponent } from '../main.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';

@Component({
  selector: 'app-lien-he-client',
  templateUrl: './lien-he-client.component.html',
  styleUrls: ['./lien-he-client.component.scss'],
})
export class LienHeClientComponent implements OnInit {
  displayedColumns = ['NgayCapNhat', 'Message', 'actions'];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    public dialog: MatDialog,
    private __service: LienHeClientService,
    private __mainComponent: MainNavComponent,
    private __snackbar: MatSnackBar,
    private __router: Router
  ) {
    localStorage.setItem(
      environment.key_storage.MENU.CLIENT,
      eMenuClient[eMenuClient.LIEN_HE_CLIENT]
    );
    this.__mainComponent.__localStorage = localStorage.getItem(
      environment.key_storage.MENU.CLIENT
    );
  }

  showProgressBar = true;
  dataSource: MatTableDataSource<LienHeClientModule>;
  _length = 0;
  _STT = 0;
  _lienHeClient: LienHeClientModel[];
  ngOnInit(): void {
    this.GetDataLienHeClient();
  }
  private GetDataLienHeClient() {
    this.showProgressBar = true;
    this.__service.getAll().subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        this._lienHeClient = data;
        this.dataSource = new MatTableDataSource(this._lienHeClient);
        this._length = this.dataSource.data.length;
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.dataSource.filterPredicate = (
          data: LienHeClientModel,
          filter: string
        ) => data.Message.toLocaleLowerCase().indexOf(filter) != -1;

      },

      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  showLogin() {
    this.__router.navigate(['login']);
  }
  viewDetail(lienHeClient: LienHeClientModel) {
    var lienHeClientReq = new LienHeClientModelReq(lienHeClient);
    const req = JSON.parse(JSON.stringify(lienHeClientReq));
    const dialogRef = this.dialog.open(XemThemLienHeClientComponent, {
      disableClose: true,
      width: '100%',
      height: '95%',
      data: {
        lienHeClient: lienHeClient,
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      this.GetDataLienHeClient();
    });
  }

  themLienHeMoi() {
    const dialogRef = this.dialog.open(AddLienHeClientComponent, {
      disableClose: true,
      width: '40rem',
      height: '98%',
      data: {},
    });
  }

  onSearchChange(searchText: String) {
    this.dataSource.filter = searchText.trim().toLowerCase();
  }
}
